(function() {
  app.behaviors.visitPage = {
    properties: {
      visit: {
        type: Object,
        notify: true,
        value: null
      },
      patient: {
        type: Object,
        notify: true,
        value: null
      }
    },
    genericNavigatedIn: function() {
      var params;
      params = this.domHost.getPageParams();
      if (params['visit']) {
        if (params['visit'] === 'new') {
          this._makeNewVisit();
        } else {
          this._loadVisit(params['visit']);
        }
      } else {
        this._notifyInvalidVisit();
      }
      if (this.visit) {
        if (params['patient']) {
          return this._loadPatient(params['patient']);
        } else if (this.visit.patientSerial) {
          return this._loadPatient(this.visit.patientSerial);
        } else {
          return this._notifyInvalidPatient();
        }
      }
    },
    navigatedOut: function() {
      this.visit = null;
      return this.patient = null;
    },
    _saveVisit: function() {
      app.db.upsert('doctor-visit', this.visit, (function(_this) {
        return function(arg) {
          var serial;
          serial = arg.serial;
          return _this.visit.serial === serial;
        };
      })(this));
      return this.domHost.showToast('Visit Saved');
    },
    arrowBackButtonPressed: function(e) {
      return this.domHost.navigateToPreviousPage();
    },
    saveButtonPressed: function(e) {
      this._saveVisit();
      return this.arrowBackButtonPressed();
    },
    _loadPatient: function(patientIdentifier) {
      var list;
      list = app.db.find('patient', function(arg) {
        var serial;
        serial = arg.serial;
        return serial === patientIdentifier;
      });
      if (list.length === 1) {
        return this.patient = list[0];
      } else {
        return this._notifyInvalidPatient();
      }
    },
    _notifyInvalidPatient: function() {
      return this.domHost.showModalDialog('Invalid Patient Provided');
    },
    _loadVisit: function(visitIdentifier) {
      var list;
      list = app.db.find('doctor-visit', function(arg) {
        var serial;
        serial = arg.serial;
        return serial === visitIdentifier;
      });
      if (list.length === 1) {
        return this.visit = list[0];
      } else {
        return this._notifyInvalidVisit();
      }
    },
    _notifyInvalidVisit: function() {
      return this.domHost.showModalDialog('Invalid Visit Provided');
    }
  };

}).call(this);
